* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: inherit;
  list-style-type: none;
}

select {
  cursor: pointer;
}

select option {
  background: #fff;
}

:root {
  --normal-size: 16px;
  --longer-size: 24px;
  --long-size: 32px;
  --longest-size: 50px;

  --inputs-size: 13px;
  --filter-height: 40px;

  --text-color: #395f7a;
}

/* ------------------
    BG and Global Styles
   ------------------ */

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background: url("assests/bg-grocery.jpg") no-repeat center center/cover;
  color: #282828;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 0 32px;
  overflow: hidden;
}

.circle {
  background: linear-gradient(
    145deg,
    rgba(255, 255, 255, 0.6),
    rgba(180, 228, 255, 0.2)
  );
  border-radius: 50%;
  filter: blur(1px);
  opacity: 0.6;
  position: absolute;
  z-index: -1;
}

.circle-left {
  width: 35vw;
  height: 30vw;
  left: -85px;
  top: 65vh;
  transform: rotate(145deg);
}

.circle-right {
  width: 30vw;
  height: 25vw;
  right: -55px;
  bottom: 62vh;
}

.container {
  padding: 80px 95px 95px;

  display: flex;
  justify-content: space-between;

  background: rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(6px);
  border-radius: 16px;
  border: 1px solid #bdbdbd;
}

.container-wrapper-left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 27.5vw;
  margin-right: 32px;
}

.container-wrapper-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 35vw;
}

.logo {
  font-size: 36px;
  background: -webkit-linear-gradient(45deg, #def3ff, #fff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  margin-bottom: 30px;
}

.logo::after {
  content: "";
  width: 165px;
  height: 120px;
  position: absolute;
  right: 75px;
  bottom: 0;
  background: url("assests/basket.svg") no-repeat center/contain;
}

.btn {
  background: linear-gradient(to right bottom, #406f89, #37f5f5);
  color: #fff;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn:hover {
  transform: scale(1.02);
}

.value {
  font-size: 24px;
  background: linear-gradient(#3d7c84, #359eab);
  color: #fff;

  display: inline-block;
  width: 40%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  border: none;
  outline: none;
  text-align: center;
  appearance: none;
}

/* ------------------
    Component Styles
   ------------------ */

/* Add Item styles */
.add-form {
  display: flex;
  flex-direction: column;
  border-right: 1px solid #508b87;
  padding-right: 28px;
}

.add-input {
  font-size: 15px;
  width: 100%;

  height: 55px;

  background: linear-gradient(#eaf6ff, #ffffff);
  color: rgba(6, 38, 61, 1);

  border-radius: 6px;
  padding: 0 16px;
  margin-bottom: 24px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.25);

  outline: none;
  border: none;
}

.add-input option {
  color: rgba(6, 38, 61, 1);
}

.values-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.values-container .add-input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 46.5%;
}

.add-input .value:hover {
  background: linear-gradient(#48919b, #3bafbe);
}

.btn-plus {
  width: 25px;
  height: 25px;
  background: url("assests/plus.svg") no-repeat center/cover;
}

.btn-minus {
  width: 25px;
  height: 25px;
  background: url("assests/minus.svg") no-repeat center/cover;
}

.btn-plus:hover,
.btn-minus:hover {
  transform: scale(1.05);
}

.btn-add {
  font-size: 18px;
  height: 55px;
  border-radius: 6px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.25);
}

.btn-transition {
  display: none;
}

/* Filter Styles */
.filter-form {
  display: grid;
  grid-template-columns: 2.5fr 2fr 1fr;
  grid-column-gap: 16px;
  border-bottom: 1px solid #508b87;
  align-items: center;
  padding: 26px 0;
}

.filter-input {
  font-size: 15px;
  width: 100%;

  background: linear-gradient(
    to right bottom,
    #ffffff,
    rgba(255, 180, 180, 0.25)
  );

  border-radius: 33px;
  border: none;
  outline: none;

  height: 45px;
  padding-left: 16px;
  margin-right: 16px;
}

.filter-form .btn {
  height: 45px;
  font-size: 15px;
  border-radius: 33px;
}

/* Show Items Styles */
.showitems {
  display: flex;
  flex-direction: column;
}

.product-list {
  height: 224px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #395f7a #ffffff;
  margin-bottom: 27px;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #395f7a;
}

.product-item {
  width: 96%;
  height: 43px;

  background: #fff;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  margin-bottom: 16px;

  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.product-item:last-child {
  margin-bottom: 0;
}

.item-checked::after {
  content: "";
  background: rgba(0, 0, 0, 0.25);
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.category-icon {
  width: 45px;
  height: 93%;
  margin-right: 10px;
  background: url("assests/temp/grocery-c.svg") no-repeat center/contain;
}

.icon-fruit {
  background: url("assests/temp/fruit-c.svg") no-repeat center/contain;
}

.icon-vegetable {
  background: url("assests/temp/vegetable-c.svg") no-repeat center/contain;
}

.icon-meat {
  background: url("assests/temp/meat-c.svg") no-repeat center/contain;
}

.product-name {
  font-size: 20px;
  font-weight: normal;
  flex: 1 0;
  margin-left: 16px;
}

.product-item .value {
  width: 80px;
  font-size: 25px;
}

.product-item .value-red {
  background: #d33c4f;
}

.product-item .value-blue {
  background: #0f80a3;
}

.btn-check,
.btn-delete {
  width: 24px;
  height: 100%;
  margin: 0 28px;
  border: none;
  outline: none;
  cursor: pointer;
}

.btn-check:hover,
.btn-delete:hover {
  transform: scale(1.1);
}

.btn-check {
  background: url("assests/check.svg") no-repeat center/contain;
}

.btn-delete {
  background: url("assests/delete.svg") no-repeat center;
}

/* Empty Basket */
.empty-basket {
  width: 340px;
  height: 50%;
  margin: 0 auto;
  font-size: 48px;
  background: -webkit-linear-gradient(45deg, #def3ff, #fff);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  align-self: center;
  text-align: center;
}

.empty-basket i {
  font-size: 64px;
  opacity: 0.8;
}

/* Products Features */
.product-features {
  width: 96%;
  height: 42px;
  display: flex;
  justify-content: space-between;
}

.value-sum {
  display: flex;
  align-items: center;
  font-size: 26px;
  color: #fff;
}

.value-sum span {
  height: 100%;
  display: flex;
  align-items: center;
  background: linear-gradient(#3d7c84, #359eab);
  border-radius: 6px;
  margin-left: 8px;
  padding: 0 8px;
}

.product-features .btn {
  font-size: 14px;
  background: linear-gradient(to right bottom, #883333, #de2e2e);
  border-radius: 6px;
  padding: 0 38px;
}

/* Mobile Version */
@media (max-width: 880px) {
  .container {
    padding: 0;
    background: none;
    border: none;
    backdrop-filter: none;
    position: relative;
  }

  .container-wrapper-left,
  .container-wrapper-right {
    width: 100%;
    margin: 0;
    padding: 35px 25px 30px;
    background: rgba(0, 0, 0, 0.35);
    backdrop-filter: blur(6px);
    border-radius: 16px;
    border: 1px solid #bdbdbd;
    transition-duration: 250ms;
  }

  .container-wrapper-right {
    position: absolute;
    left: 100vw;
  }

  .transition-in {
    transform: translateX(-100vw);
  }

  .transition-out {
    transform: translateX(-100vw);
  }

  .value {
    font-size: 20px;
  }

  .logo {
    width: 260px;
    font-size: 26px;
    margin-bottom: 18px;
    padding-top: 20px;
  }

  .logo::after {
    width: 130px;
    height: 100px;
    right: 0;
  }

  .add-form {
    padding: 16px 0 0;
    border: none;
    border-top: 1px solid #508b87;
  }

  .add-input {
    font-size: 12px;
    height: 45px;
    margin-bottom: 16px;
    padding: 0 11px;
  }

  .values-container .add-input {
    width: 47%;
  }

  .btn-minus,
  .btn-plus {
    width: 20px;
    height: 20px;
  }

  .btn-add {
    font-size: 16px;
    height: 45px;
    margin-bottom: 8px;
  }

  .btn-transition {
    display: block;

    width: 100%;
    height: 40px;
    font-size: 14px;
    border-radius: 6px;
    background: #114a52;
  }

  .filter-form {
    grid-template-columns: 2fr 1fr;
    grid-gap: 0;
    grid-row-gap: 8px;
    padding: 0 0 16px 0;
    margin-bottom: 18px;
  }

  .filter-input {
    font-size: 13px;
    height: 36px;
    order: 2;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }

  .filter-form select {
    order: 1;
    grid-column: 1/3;
    border-radius: 33px;
  }

  .filter-form .btn {
    font-size: 15px;
    height: 36px;
    order: 3;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    transform: none;
  }

  .filter-form .btn:hover {
    background: linear-gradient(to right bottom, #4b82a0, #37f5f5);
  }

  .empty-basket {
    font-size: 32px;
    width: 220px;
    height: 190px;
    margin-top: 60px;
  }

  .empty-basket i {
    font-size: 32px;
  }

  .showitems {
    margin-bottom: 15px;
  }

  .product-list {
    height: 205px;
    margin-bottom: 15px;
  }

  .product-item {
    height: 32px;
    margin-bottom: 11px;
  }

  .category-icon {
    margin-right: 0;
  }

  .product-name {
    font-size: 17px;
  }

  .product-item .value {
    width: 35px;
    font-size: 20px;
  }

  .btn-check,
  .btn-delete {
    width: 18px;
    height: 100%;
    margin: 0 8px;
  }

  .product-features {
    width: 100%;
    height: 33px;
  }

  .value-sum {
    font-size: 20px;
  }

  .product-features .btn {
    font-size: 13px;
    padding: 0 17px;
  }
}
